import React from 'react';
import { FaHeart } from 'react-icons/fa';
import '../assets/css/Upvotes.css';

const UpvotesSection = ({ articleName, upvotes, setArticleInfo }) => {
    const upvoteArticle = async () => {
        const result = await fetch(`/api/articles/${articleName}/upvote`, {
            method: 'post',
        });
        const body = await result.json();
        setArticleInfo(body);
    }

      
    return (
        
        <div id="upvotes-section">
            <button type="button" className="btn btn-outline-light btn-circle btn-xl" onClick={() => upvoteArticle()}><FaHeart size="1x"/></button>
            <h3><span>Love the project?</span><br></br> Send me a heart {upvotes}. Thank you!</h3>
        </div>
    );
}

export default UpvotesSection;