import React, { useState, useEffect } from 'react';
import ArticlesList from '../components/ArticlesList';
import CommentsList from '../components/CommentsList';
import UpvotesSection from '../components/UpvotesSection';
import AddCommentForm from '../components/AddCommentForm';
import NotFoundPage from './NotFoundPage';
import articleContent from './article-content';
import Logo from '../assets/img/logo.png';
import '../assets/css/ArticlePage.css';
import FooterPage from './FooterPage';

const ArticlePage = ({ match }) => {
    const name = match.params.name;
    const article = articleContent.find(article => article.name === name);

    const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });

    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();
            setArticleInfo(body);
        }
        fetchData();
    }, [name]);

    if (!article) return <NotFoundPage />

    const otherArticles = articleContent.filter(article => article.name !== name);

    return (
        <>

    <div className="main-container">
        <div className="left-container">
            <div>
                <img id="logo"  src={Logo} alt="main-logo"/>
            </div>
        </div>
    </div> 

    <section id="hero" className="d-flex justify-cntent-center align-items-center">
    <div className="hero-container"><img src={article.headerImage} alt="" className="img-fluid"/> 
    </div>
    </section>

    <div className="card-body text-dark">
                <p className="card-text text-secondary">
                    {article.content.map((paragraph, key) => (
                        <p key={key}>{paragraph}</p>
            ))}
            </p>
               
     </div>
     <section className="middle-container">
            <div className="row">
                    
                    <div className="d-flex justify-content-center">
                   
                        <img src={article.middleImage} alt="" className="img-fluid"/>
                    </div>

            </div>

     </section>

     <section className="middle-container">
            <div className="row">
                    
                    <div className="d-flex justify-content-center">
                   
                        <img src={article.tileImage} alt="" className="img-fluid"/>
                    </div>

            </div>

     </section>

     <section className="like-container">
         <div className="row">
             <div className ="col-lg-12">
             <UpvotesSection articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo} />
        </div>
    </div>
    </section>

    <section className ="comments-container">
        
        <CommentsList comments={articleInfo.comments} />
        <AddCommentForm articleName={name} setArticleInfo={setArticleInfo} />

     </section>   
     <section className="more-projects">
        <h3 className="other-articles">More Projects:</h3>
        <ArticlesList articles={otherArticles} />
            
        </section> 
            <FooterPage/>

        </>
    );
}

export default ArticlePage;